














































































	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
	import wxCharts from '../../components/wx-charts/wxcharts.js'
	var _self;
	var canvaColumn = null;
	var canvaLineA = null;
	var canvaLineB = null;
	var canvaArea = null;
	var Data = {
		LineA: {
			categories: ['2012', '2013', '2014', '2015', '2016', '2017', '2018'],
			series: [{
				name: '',
				data: [35, 20, 25, 37, 4, 20, 10]
			}, {
				name: '',
				data: [70, 40, 65, 90, 44, 68, 20]
			}]
		},

		Pie: {
			series: [{
				name: '',
				data: 50
			}, {
				name: '',
				data: 30
			}, {
				name: '',
				data: 20
			}, {
				name: '',
				data: 18
			}, {
				name: '',
				data: 8
			}]
		}
	}
	export default {
		components: {
			uniLoadMore,
			wxCharts
		},
		data() {
			return {
				cWidth: '',
				cHeight: '',
				pixelRatio: 1
			}
		},
		onLoad() {
			_self = this;











			this.cWidth = uni.upx2px(750);
			this.cHeight = uni.upx2px(500);
            this.changeTab()

		},
		onReady() {
			this.showLineA("canvasLineA", Data.LineA);
			this.showPie("canvasPie", Data.Pie);
		},
		methods: {
			showLineA(canvasId, chartData) {
				canvaLineA = new wxCharts({
					canvasId: canvasId,
					type: 'line',
					fontSize: 11,
					legend: true,
					background: '#FFFFFF',
					pixelRatio: _self.pixelRatio,
					categories: chartData.categories,
					animation: true,
					series: chartData.series,
					xAxis: {
						disableGrid: true,
					},
					yAxis: {
						//disabled:true
					},
					width: _self.cWidth * _self.pixelRatio,
					height: _self.cHeight * _self.pixelRatio,
					dataLabel: true,
					dataPointShape: true,
					extra: {
						lineStyle: 'straight'
					},
				});

			},
			showLineB(canvasId, chartData) {
				canvaLineB = new wxCharts({
					canvasId: canvasId,
					type: 'line',
					fontSize: 11,
					legend: true,
					background: '#FFFFFF',
					pixelRatio: _self.pixelRatio,
					categories: chartData.categories,
					animation: true,
					series: chartData.series,
					xAxis: {
						disableGrid: true,
					},
					yAxis: {
						//disabled:true
					},
					width: _self.cWidth * _self.pixelRatio,
					height: _self.cHeight * _self.pixelRatio,
					dataLabel: true,
					dataPointShape: true,
					extra: {
						lineStyle: 'curve'
					},
				});
			},
			showPie(canvasId, chartData) {
				new wxCharts({
					canvasId: canvasId,
					type: 'pie',
					fontSize: 11,
					legend: true,
					background: '#FFFFFF',
					pixelRatio: _self.pixelRatio,
					series: chartData.series,
					animation: true,
					width: _self.cWidth * _self.pixelRatio,
					height: _self.cHeight * _self.pixelRatio,
					dataLabel: true,
				});
			},
			touchLineA(e) {
				canvaLineA.showToolTip(e, {
					format: function(item, category) {
						return category + ' ' + item.name + ':' + item.data
					}
				});
			},
			touchLineB(e) {
				canvaLineB.showToolTip(e, {
					format: function(item, category) {
						return category + ' ' + item.name + ':' + item.data
					}
				});
			},
			addshenqing: function() {
				uni.navigateTo({
					url: '../normaluser/addshenqing/addshenqing'
				})
			},
			changeTab() {
				console.log(1)
				uni.getStorage({
					key: "isnomaluser",
					success: function(res) {
						if (res.data == 1) {
							uni.setTabBarItem({
								index: 2,
								text: '',
							})
						} else {
							uni.setTabBarItem({
								index: 2,
								text: ''
							})
						}
					}
				})
			}

		},
		onPullDownRefresh: function() {
			// 
			setTimeout(function() {
				uni.stopPullDownRefresh()
			}, 1000)
		}
	}
